// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Lib from '@/assets/js/Lib';
import { Toast } from 'mint-ui';
// require('@/assets/css/index.css');
// import'@/assets/css/index.css';
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  data() {
    return {
      busId: 0,
      url: '',
      currUrl: '',
      urlCardNo: '',
      urlShare: '',
      shareTitle: '您的好友给您推荐一张会员卡，赶紧领取吧!',
      shareDesc: '识别图中二维码领取会员卡',
      shareImg: 'http://wx.qlogo.cn/mmopen/ib5Ef9ImF1HN9hPsiaBlibGe5iaQDGFtJ4J33IicwtlB8YxSWzfLrmBNqjsdqOUM3MFtJpuZRdpNhwIqguw0D5ibOhxaFKBlsP20De/0'
    }
  },
  watch: {
    // 监听 $route 变化调用分享链接
    "$route" (to, from) {
      let currentRouter = this.$router.currentRoute.fullPath; //
      console.log('hdz',window.location.href)
      console.log('currentRouter', currentRouter);
      if (currentRouter.indexOf('qrcode') == -1) { //如果不是userShare分享页面,则分享另外一个接口

      } else {
        this.loadData(); //当前页面是userShare页面时分享调用另外一个接口      
      }
    }
  },
  methods: {
    loadData: function() {
      var _this = this,
        json = {};
      _this.busId = _this.$route.params.busId;
      _this.currUrl = window.location.href;

      _this.urlShare = memberAPI.activeAPI.goURL + memberAPI.activeAPI.goMen + '?#/shareQRCode/' + _this.busId;
      // _this.currUrl = memberAPI.activeAPI.goURL + memberAPI.activeAPI.goMen + '#/qrcode/' + _this.busId;
      alert(_this.currUrl)
      json["busId"] = _this.busId;
      json["requestUrl"] = _this.currUrl;
      console.log("_this.urlShare", json);
      Lib.M.ajax({
          'url': memberAPI.activeAPI.wxshareCard_POST,
          'type': 'POST',
          'data': {
            "json": json
          },
          'success': function(data) {
            // alert(JSON.stringify(data))
            console.log('shareQRCode', data)
            if (data.code == 0) {
              wx.config({
                debug: true, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
                appId: data.data.appid, // 必填，公众号的唯一标识
                timestamp: data.data.timestamp, // 必填，生成签名的时间戳
                nonceStr: data.data.nonce_str, // 必填，生成签名的随机串
                signature: data.data.signature, // 必填，签名，见附录1
                jsApiList: ["onMenuShareAppMessage"] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
              });
              wx.ready(function() {
                //分享给朋友
                alert( _this.urlShare);
                wx.onMenuShareAppMessage({
                  title: _this.shareTitle, // 分享标题
                  desc: '123', // 分享描述
                  link: _this.urlShare, // 分享链接
                  imgUrl: _this.shareImg, // 分享图标
                  success: function() {
                    // 用户确认分享后执行的回调函数
                    Toast({
                      message: '分享成功',
                      duration: 2000
                    });
                  },
                  cancel: function() {
                    // 用户取消分享后执行的回调函数
                    Toast({
                      message: '取消分享',
                      duration: 1000
                    });
                  },
                  error: function(res) {
                    alert(res)
                  }
                });
              });
              wx.error(function(res) {
                  alert ('err', res)
              });
          }
        }
      })
  }
},
components: { App }
})



