<template>
		<div class="tradingRecord qrcode">
			<div class="clearfix topuphead">
				<a @click="$router.go(-1)" class="ago fl"><i class="icon iconfont icon-angle-left"></i></a>
				<router-link tag="a"  :to="{name:'home',params: { busId: busId }}" class="more fr" >
					<a >首页</a>
				</router-link>
			</div>
			<div class="OfferPay">
				<p>把二维码发给好友，让好友一起加入会员行列吧！</p>
			</div>
			<div class="offerPayCom">
				<div class="OfferPayQR">
					<img :src="urlCardNo" width="88%">
				</div>
				<p>转发此页面给好友，加入会员行列</p>
			</div>
		</div>
</template>
<script>
import Lib from '@/assets/js/Lib';
import { Toast } from 'mint-ui';
	export default{
		data() {
		    return {
		    	busId:0,
		    	url:'',
		    	currUrl:'',
		    	urlCardNo:'',
		    	urlShare:'',
		    	shareTitle:'您的好友给您推荐一张会员卡，赶紧领取吧!',
		    	shareDesc:'识别图中二维码领取会员卡',
		    	shareImg:'http://wx.qlogo.cn/mmopen/ib5Ef9ImF1HN9hPsiaBlibGe5iaQDGFtJ4J33IicwtlB8YxSWzfLrmBNqjsdqOUM3MFtJpuZRdpNhwIqguw0D5ibOhxaFKBlsP20De/0'
			}
		},
		methods:{
			loadData:function(){
				var _this=this,json={};
				_this.busId= _this.$route.params.busId;
				_this.currUrl=window.location.href;
				// _this.urlShare=memberAPI.activeAPI.goURL+memberAPI.activeAPI.goMen+'#/shareQRCode/'+_this.busId;
				// _this.url=_this.url[0]+'#/home/'+_this.busId;
				// _this.url=memberAPI.activeAPI.goURL+'/html/phone/index.html#/home/'+_this.busId;
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.currUrl;
				_this.urlCardNo=memberAPI.activeAPI.homeUrl+memberAPI.activeAPI.tuijianQRcode_GET+"?busId="+_this.busId;

			}
		},
		created:function(){
	    	var _this=this;
	    	
	    },
	    mounted:function(){
	    	var _this=this;
	    	_this.loadData();
		}
		
	}
</script>
<style scope>

</style>


